// ==========================================================================
// Carousel Layout
// ==========================================================================

.carousel {
    @include section-colors("carousel");
    height: 550px;

    background-image: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(../img/carousel.jpg);              // Apply a 50% darken to the whole background
    @media (min-width: $breakpoint-md) {                                                                            // At screen width 992+
        background-image: linear-gradient(to right, rgba(0,0,0,1), rgba(0,0,0,0)), url(../img/carousel.jpg);        // Fade in black from black on the left side
    }
    background-size: cover;


    text-align: center;

    .flex-container {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    h1 {
        line-height: 80px;
        margin: 0;
        width: 100%;
        @media (min-width: $breakpoint-md) {    // At screen width 992+
            font-size: 64px;                        // Bump the font size up
            text-align: left;                       // and align to the left
        }
    }

    p {
        font-size: 20px;
        font-weight: 600;
        width: 100%;
        margin: 10px 0;
        @media (min-width: $breakpoint-md) {    // At screen width 992+
            font-size: 26px;                        // Bump the font size up
            text-align: left;                       // and align to the left
        }
    }

    .button {
        @include btn($color-white, map-get($service-colors, "bespoke-software"), 20px, 5px, 20px, true);
        margin-top: 30px;
        line-height: 50px;
        @media (min-width: $breakpoint-md) {    // At screen width 992+
            margin-right: auto;                     // Align to the left
        }
    }
}